.App {
  display: flex;
  align-content: center;
  justify-content: center;
}

.calc {
  display: flex;
  flex-direction: column;
}

.button {
  border: 1px solid black;
  font-size: 2em;
  text-align: center;
  line-height: 200%;
  flex: 1;
  cursor: pointer;
  background: #bbbbbb;
}

.button:hover {
  background-color: grey;
}

.row {
  display: flex;
  width: 400px;
}

input {
  text-align: center;
  background: black;
  color: orange;
  height: 3em;
  outline: solid 1px black;
  font-size: 2em;
}
